import { UserService } from './../../SERVICES/user.service';
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { firestore } from 'firebase/app'

@Component({
  selector: 'app-bene-details',
  templateUrl: './bene-details.page.html',
  styleUrls: ['./bene-details.page.scss'],
})
export class BeneDetailsPage implements OnInit {
  postID: string
  post
  constructor(private route: ActivatedRoute, private user: UserService, private afs: AngularFirestore, public router: Router,) { }

  ngOnInit() {
    this.postID = this.route.snapshot.paramMap.get('id')
    this.post =this.afs.doc(`publicevents/${this.postID}`).valueChanges()
  } 

  Participer(){
this.afs.collection("publicevents").doc(this.postID).update({
      participants:[this.user.getUID()]
    })
      .then(function () {
        console.log("Document successfully written!");
        
      })
      .catch(function (error) {
        console.error("Error writing document: ", error);
      });
      this.router.navigate(['/bene-tabs/bene-my-events']);
  }

  RemoveParticiper(){
  this.afs.collection("publicevents").doc(this.postID)
   .update({
    participants: firestore.FieldValue.arrayRemove(this.user.getUID())
  })
  .catch(function(error) {
      console.error("Error removing document: ", error);
  });
  }
  

}
